.slider-wrap{
    position: relative;
    height: calc(100vh - 270px);
    min-height: 500px;
    .swiper {
        height: 100%;
    }
    .parent-wrap{
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .left-wrap{
            h2{
                font-size: 50px;
                font-weight: 700;
                color: #2E4064;
            }
            p{
                font-size: 25px;
                font-weight: 700;
                color: #2E4064;
                padding: 20px 0 40px;
            }
            .btn{
                background-color: #2E4064;
                position: relative;
                font-size: 20px;
                padding: 10px 60px;
                border-radius: 5px;
                cursor: pointer;
                color:#fff;
                text-decoration: none;
                font-weight: 700;
                }
            }
        .right-wrap > img {
            width: 70%;
            margin: 0 15%;
        }
    }
    @media only screen and (max-width: 800px){
        min-height: 630px;
        .parent-wrap{
            text-align: center;
            flex-flow: column;
            justify-content: flex-start;
            .left-wrap{
                h2{
                    font-size: 45px;
                }
                p{
                    font-size: 21px;
                }
            }
            .right-wrap{
                img{
                    width: 100%;
                    margin: 40px auto 0;
                    height: 250px;
                }
            }
        }
    }
}