.create-exp-wrap{
    h4 {
        text-align: center;
        font-size: 24px;
        font-weight: 700;
        color: #2E4064;
        padding: 40px 0;
    }
    .inner-wrap {
        padding: 20px 40px;
        border: solid 1px #2E4064;
        border-radius: 30px;
        width: 100%;
        max-width: 300px;
        text-align: center;
        margin: 0 auto;
        input[type=text], input[type=number] {
            height: 40px;
            outline: none;
            border-radius: 20px;
            padding: 0 20px;
            width: 100%;
            text-align: center;
            border: solid 1px #e3d7d7;
            text-transform: capitalize;
        }
        input[type=number]{
            margin-top: 5px;
            &::-webkit-outer-spin-button, &::-webkit-inner-spin-button {
                -webkit-appearance: none;
                margin: 0;
            }
        }
        .paid-by{
            padding: 10px 0;
            .ttl{
                font-size: 14px;
            }
            .prsn{
                padding: 5px 9px;
                background: #afacac;
                border-radius: 20px;
                font-size: 13px;
                font-weight: 600;
            }
        }
        .added-members {
            margin: 20px 0;
            background-color: #fff;
            border-radius: 20px;
            min-height: 100px;
            position: relative;
            padding: 20px;
            ul {
                height: 100%;
                position: relative;
                text-align: left;
                min-height: 60px;
                li{
                    &.no-mbrs {
                        position: absolute;
                        left: 50%;
                        top: 50%;
                        width: 100%;
                        font-size: 12px;
                        transform: translate(-50%, -50%);
                        text-align: center;
                    }
                    &.member {
                        display: flex;
                        align-items: center;
                        justify-self: center;
                        margin-top: 7px;
                        position: relative;
                        &:first-child {
                            margin-top: 0;
                        }
                        span {
                            padding: 5px 29px 5px 15px;
                            background: #c2c2c2;
                            border-radius: 20px;
                            font-size: 13px;
                            font-weight: 600;
                            width: 100%;
                            cursor: pointer;
                            position: relative;
                            transition: all 450ms;
                            &:hover{
                                box-shadow: 0px 0px 5px 3px rgba(0,0,0,0.68);
                                -webkit-box-shadow: 0px 0px 5px 3px rgba(0,0,0,0.68);
                                -moz-box-shadow: 0px 0px 5px 3px rgba(0,0,0,0.68);
                            }
                            &:after{
                                content: '';
                                width: 10px;
                                height: 10px;
                                position: absolute;
                                right: 10px;
                                top: 50%;
                                transform: translateY(-50%);
                                background: url('../../../../public/images/remove.png') center / 100% 100% no-repeat;
                            }
                        }
                    }
                }
            }
        }
        .members-wrap {
            text-align-last: left;
            ul{
                li {
                    display: flex;
                    align-items: center;
                    justify-self: center;
                    margin-top: 7px;
                    position: relative;
                    &:first-child {
                        margin-top: 0;
                    }
                    span {
                        padding: 5px 29px 5px 15px;
                        background: #c2c2c2;
                        border-radius: 20px;
                        font-size: 13px;
                        font-weight: 600;
                        width: 100%;
                        cursor: pointer;
                        position: relative;
                        transition: all 450ms;
                        &:hover{
                            box-shadow: 0px 0px 5px 3px rgba(0,0,0,0.68);
                            -webkit-box-shadow: 0px 0px 5px 3px rgba(0,0,0,0.68);
                            -moz-box-shadow: 0px 0px 5px 3px rgba(0,0,0,0.68);
                        }
                        &:after{
                            content: '';
                            width: 10px;
                            height: 10px;
                            position: absolute;
                            right: 10px;
                            top: 50%;
                            transform: translateY(-50%);
                            background: url('../../../../public/images/add.png') center / 100% 100% no-repeat;
                        }
                    }
                }
            }
        }
        .create-btn {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            text-align: center;
            height: 40px;
            background: #2E4064;
            border-radius: 20px;
            color: #fff;
            font-size: 18px;
            font-weight: 700;
            cursor: pointer;
            transition: all 450ms;
            border: none;
            margin-top: 20px;
        }
    }
}